import React, { useState } from "react";
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';


function CreateArea(props) {

  const [isExpanded,setIsExpanded]=useState(false)
  

  function expand() {
  setIsExpanded(true);

  }

  return (
    <div>
      <form className='create-note'>
        {isExpanded? <input onChange={props.handleChange} value={props.title} name="title" placeholder="Title" />:null}
        <textarea onClick={expand} onChange={props.handleChange} value={props.content} name="content" placeholder="Take a note..." rows={isExpanded?3:1} />

        {isExpanded? <Zoom timeout={500} in='true'><Fab onClick={props.addNote}><AddIcon/></Fab></Zoom>:null}
      
      </form>
    </div>
  );
}

export default CreateArea;
