import React, { useState } from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import CreateArea from "./CreateArea";


function App() {

//useState always retuens an array
const [note,setNote]=useState({title:"",content:''}); //note is object 
const[notes,setNotes]=useState([]); //notes is an arrau of note objects

function handleChange(event) {
const {name,value}=event.target; //event.target is an object
console.log('changed');
  setNote((prevValue)=>{
    return {
      ...prevValue,
      [name]:value
    };
  });
}

function addNote(event) {
  event.preventDefault();
setNotes((prevValue)=>{
return [...prevValue,note]
});
setNote({title:'',content:''});
}

function deleteNote(id) {
  
 setNotes(()=>{
  return notes.filter((item,index)=>{
    return index!==id;
  });
 })

}

  return (
    <div>
      <Header />
      <CreateArea  title={note.title} content={note.content} handleChange={handleChange} addNote={addNote}/>
     {notes.map((item,index)=>( //note is an object
       <Note key={index} id={index} title={item.title} content={item.content} deleteNote={deleteNote}/>
     ))}
      <Footer />
    </div>
  );
}

export default App;
